
.BannerSlider{

	.slide{
		background-attachment: fixed;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		position: relative;
		min-height: 75vh;

		img{
			position: absolute;
			top: 0;
			bottom: 0;
			height: 100%;
			width: 100%;
			object-fit: cover;
		}

		.centerSlide{
			margin: 264px 0 158px 0;
			text-align: center;
			z-index: 2;
			position: relative;
		}
	}

	.slick-dots{
    position: absolute;
    top: 156px;
    width: 84px;
    padding: 0;
    left: 50%;
    right: auto;
    margin: 0;
    margin-left: -42px;
    height: 84px;

		li{
			display: flex;
			list-style: none;
			position: absolute;
			width: 25px;
			height: 25px;
			align-items: center;
			justify-content: center;

			button{
				font-size:0;
				width:11px;
				height:11px;
				background:#fff;
				border-radius: 50%;
				padding:0;
				margin:0;
				transition: all .7s ease;
				outline: none;
				border: none;
			}
		}


		li:nth-of-type(1){
			transform: translateX(60px)
		}

		li:nth-of-type(2){
			transform: translateY(-30px) translateX(30px)
		}

		li:nth-of-type(3){
			transform: translateX(0px)
		}

		li:nth-of-type(4){
			transform: translateY(30px) translateX(30px)
		}



		li.slick-active{
			button{
				width:25px;
				height:25px;
			}
		}

		li:hover{
			button{
				width:25px;
				height:25px;
				background:#27d290;
			}
		}

	}

}

