.dotnav-right {
	left: 80px;
	top: 50%;
	margin-top: -82px;
	height: auto;
	z-index: 999;
	position: fixed;
	padding: 0;
	list-style: none;
	font-size: 0.001px;
	width: 20px;

	li {
		display: inline-block;
		font-size: 1rem;
		vertical-align: top;
		width: 20px;
		height: 20px;
		margin-top: 16px;


		a {
			display: inline-block;
			-moz-box-sizing: content-box;
			box-sizing: content-box;
			width: 16px;
			height: 16px;
			border-radius: 50%;
			border: 2px solid #fff;
			vertical-align: top;
			overflow: hidden;
			padding: 0;
			text-indent: -999%;
			padding:0;

		}
		a:hover,
		a:focus {
			background: #fff;
			outline: none;
		}

	}





}




.dotnav > li > a:active,
.dotnav > li.active > a  {
