@font-face {
	font-family: SpaceGrotesk;
	font-weight: 400;
  src:  url('./inc/SpaceGrotesk-Regular.woff2') format('woff2'), /* Super Modern Browsers */
				url('./inc/SpaceGrotesk-Regular.woff') format('woff') /* Pretty Modern Browsers */
}

@font-face {
	font-family: SpaceGrotesk;
	font-weight: 500;
  src:  url('./inc/SpaceGrotesk-Medium.woff2') format('woff2'), /* Super Modern Browsers */
				url('./inc/SpaceGrotesk-Medium.woff') format('woff') /* Pretty Modern Browsers */
}
