section{
	min-height: 50vh;
}

body{
	font-family: SpaceGrotesk;
}

.container{
	width: 80%;
	margin: 0 auto;
}


.botaoBorda{
	padding: 15px 24px;
	border-radius: 24px;
	border: solid 2px #ffffff;
	font-family: SpaceGrotesk;
	font-size: 16px;
	font-weight: 500;
	font-stretch: normal;
	font-style: normal;
	line-height: normal;
	letter-spacing: normal;
	text-align: center;
	text-decoration: none;
	color: #ffffff;
	margin-top: 32px;
	display: inline-block;
	transition: all .6s ease;
}

