

.containerLarge{
    width:100%;
    padding:0 64px;

}







#masthead{
    position: fixed;
    top:0;
    width:100%;
    z-index:2;

    .site-branding{
        float:left;
    }
}

























header#masthead {
    padding: 10px 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    transition: all 0.2s ease-in-out;
    background: #fff;
}



header#masthead.menuTopo{
    background:transparent;
}




.site-branding {
    float: left;
    display: flex;

    img, svg {
        margin-right: 14px;
    }
    p {
        font-size: 10px;
        line-height: 12px;
        color: #838383;
        margin-top: -3px;
    }

    strong {
        display: block;
    }
}

#site-navigation {
    float: right;
    width: auto;

    ul li {
        margin: 0 32.5px !important;
        overflow: hidden;
    }

    li .sub-menu {
        padding: 0;
        margin: 0;
        position: absolute;
        display: none;
        top: 0;
        box-shadow: none;
        padding-top: 15px;
        z-index: -1;
        padding-bottom: 0;

        li {
            margin: 0 0 25px 0 !important;
        }

        a {
            text-transform: none;
        }

        a::after {
            display: none;
        }
    }

    ul a {
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.12em;
        text-transform: uppercase;
        color: #2c3231;
        position: relative;
    }

    ul a::after {
        content: "";
        width: 0;
        height: 1px;
        background: #eb5c37;
        display: block;
        position: absolute;
        bottom: -10px;
        left: 0;
        right: 0;
        transition: all 0.7s ease;
    }

    ul a:hover::after {
        width: 100%;
    }
}


.nav-up {
    top: -110px !important;
}

body.on header#masthead{
    background: transparent !important;
}

body.on{
    overflow: hidden;
}

.menuTopo{

    svg use{
        fill: #FFF;
    }
    background: transparent !important;
}


nav ul a:hover {
    background-color: transparent !important;
}



@media screen and (min-width: 1281px) {


    li .sub-menu {
        display: block !important;
    }


    #site-navigation li .sub-menu li{
        overflow: hidden;
        transition: all .7s ease;
        opacity: 0 !important;
    }

    #site-navigation li:hover .sub-menu li{
        opacity: 1 !important;
    }

    #site-navigation li:hover .sub-menu li:nth-of-type(2){
        transition-delay: .15s;
    }
    #site-navigation li:hover .sub-menu li:nth-of-type(3){
        transition-delay: .3s;
    }
    #site-navigation li:hover .sub-menu li:nth-of-type(4){
        transition-delay: .45s;
    }
    #site-navigation li:hover .sub-menu li:nth-of-type(5){
        transition-delay: .6s;
    }
    #site-navigation li:hover .sub-menu li:nth-of-type(6){
        transition-delay: .75s;
    }

    #site-navigation ul li {
        overflow: visible;
    }

    #site-navigation li .sub-menu {
        padding-top: 43px;
    }
}


@media screen and (max-width: 1280px) {


    header#masthead{
        padding:37px 0 17px 0;

        .containerMaze {
            padding: 0 27px;
        }
    }


    #site-navigation li .sub-menu{
        z-index:1;
    }

    body.on .site-branding p {
        pointer-events: none;
    }

    .site-branding {
        z-index: 1;
        position: relative;
    }

    #site-navigation {
        ul a {
            width: auto;
            display: inline-block;
        }

        ul li {
            margin: 0 0 !important;
            padding: 20px 0;
            border-bottom: 1px solid #000;
            position: relative;

            ul.sub-menu {
                position: relative;
                display: none;
                width: 100%;
                left: auto;

                li {
                    border: none !important ;
                    border: none !important;
                    padding: 14.5px 10px;
                    margin: 0 !important;
                }
            }
        }

        ul li:after {
            content: "";
            width: 10px;
            height: 10px;
            display: block;
            background-image: url(./images/plus-menu.svg);
            position: absolute;
            right: 0;
            top: 26px;
            background-size: 100%;
            background-repeat: no-repeat;
            background-position: center;
        }

        ul ul.sub-menu {
            li:after {
                display: none !important;
            }
        }

        ul li.aberto:after {
            background-image: url("./images/minus-menu.svg");
            border-bottom: 0 !important;
        }
    }

    .main-navigation {
        ul {
            display: none;
        }

        .menu-main-container {
            background: rgba(237, 235, 232, 0.9);
            position: fixed;
            width: 100%;
            left: 0;
            height: 100%;
            top: 0;
            visibility: hidden;
            padding-top: 115px;
            z-index: 0;

            ul {
                width: 100%;
                max-width: 1540px;
                margin: 0 auto;
                left: 0%;
                padding: 0 27px;
            }
        }
    }

    .main-navigation.toggled {
        ul.menu {
            display: block !important ;
        }
        .menu-main-container {
            visibility: visible;
        }
    }

    .menu-toggle {
        width: 22px;
        height: 20px;
        position: absolute;
        right: 25px;
        cursor: pointer;
        padding: 0 !important;
        outline: none !important;
        border: none !important;
        background-color: transparent !important;
        display: block;
        z-index: 20;
        top: 42px;
    }

    .menu-toggle.on {
        .one {
            -moz-transform: rotate(45deg) translate(4px, 4px);
            -ms-transform: rotate(45deg) translate(4px, 4px);
            -webkit-transform: rotate(45deg) translate(4px, 4px);
            transform: rotate(45deg) translate(4px, 4px);
        }
        .three {
            -moz-transform: rotate(-45deg) translate(2px, -4px);
            -ms-transform: rotate(-45deg) translate(2px, -4px);
            -webkit-transform: rotate(-45deg) translate(2px, -4px);
            transform: rotate(-45deg) translate(2px, -4px);
        }
    }

    .one,
    .three {
        margin: 0 !important;
    }

    .one,
    .two,
    .three {
        width: 100%;
        height: 2px;
        background: #838383;
        margin: 3px auto;
        backface-visibility: hidden;
        -moz-transition-duration: 0.3s;
        -o-transition-duration: 0.3s;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        border-radius: 5px;
    }

    .two {
        opacity: 0;
    }

    nav ul.hidden {
        display: none;
    }

    nav ul a {
        -moz-transition-duration: 0.5s;
        -o-transition-duration: 0.5s;
        -webkit-transition-duration: 0.5s;
        transition-duration: 0.5s;
        text-decoration: none;
        color: white;
        font-size: 3em;
        line-height: 1.5;
        width: 100%;
        display: block;
    }

    nav ul a:hover {
        background-color: rgba(0, 0, 0, 0.5);
    }
}
